	module.exports = {"data":{"markdownRemark":{"html":"<p>晚上散步，一大圈，现在天气越来越热了，但是这里的绿化还是那样的郁郁葱葱。天气炎热，池塘里的鱼都浮上了水面，在那里悠闲地浮着，也没有动静，似乎所有的世界的争斗都停止了。看着它们，一种闲情雅趣油然而生。得诗一首：</p>\n<p>山上怀古</p>\n<p>曲径漫步树飘香，</p>\n<p>波静云高山色祥。</p>\n<p>后弃闲鱼水上戏，</p>\n<p>前听飞鹊林中翔。</p>\n<p>千端世事均度外，</p>\n<p>一刻心情唯炎凉。</p>\n<p>暮色轻携别石路，</p>\n<p>桥斜古道马轿响。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0113.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0113.md","frontmatter":{"title":"6月13日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0113.md absPath of file >>> MarkdownRemark"}}

